import React from "react"
import Typist from "react-typist"

import Layout from "../components/Layout"
import { experiences } from "../constants"

export default function About() {
  return (
    <Layout>
      <div className="about-container">
        <div className="about">
          <Typist
            cursor={{
              show: false,
            }}
          >
            <Typist.Delay ms={100} />
            <h2>Web Developer</h2>
          </Typist>
          <p className="fade-in">
            Dedicated and Creative Web Developer with hands-on experience in
            modern technologies for building and optimizing performance of
            user-centric, high impact web applications. Also a Great team player
            and a decent designer.
          </p>
          <ul className="fade-in">
            <li>
              <a
                rel="noreferrer"
                href="https://www.facebook.com/18ganapathy04/"
                target="_blank"
              >
                <i className="fa fa-facebook" aria-hidden="true" />
              </a>
            </li>
            <li>
              <a
                rel="noreferrer"
                href="https://github.com/GanapathyPT"
                target="_blank"
              >
                <i className="fa fa-github" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a
                rel="noreferrer"
                href="https://twitter.com/Ganapathy_pt"
                target="_blank"
              >
                <i className="fa fa-twitter" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a
                rel="noreferrer"
                href="https://www.instagram.com/ganapathy_pt/"
                target="_blank"
              >
                <i className="fa fa-instagram" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a
                rel="noreferrer"
                href="https://www.linkedin.com/in/ganapathy-pt/"
                target="_blank"
              >
                <i className="fa fa-linkedin" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a rel="noreferrer" href="mailto:18ganapathy04@gmail.com">
                <i className="fa fa-envelope" aria-hidden="true"></i>
              </a>
            </li>
          </ul>
        </div>
        <div className="experience">
          <Typist
            cursor={{
              show: false,
            }}
          >
            <h2>Experience</h2>
          </Typist>
          <ul className="fade-in">
            {experiences.map((experience, index) => (
              <li key={index}>
                {experience.title} <br />
                <small>-- {experience.industry}</small>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </Layout>
  )
}
